import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import { MonthValue } from '../../types';

import commonStyles from '../../styles/common.scss';

const cdCommon = cssDecorator(commonStyles);

export interface MonthsGridProps {
  /** Месяцы для отображения */
  items: MonthValue[];

  /** Изменён выбранный месяц */
  onChange: (value: MonthValue, index?: number) => void;
}

/** Сетка месяцев */
export function MonthsGrid({
  items,
  onChange
}: MonthsGridProps): JSX.Element {
  const months = items.map((month, index) => {
    const classes = cdCommon(
      'cell',
      {
        hover: !month.disabled && !month.selected,
        disabled: month.disabled,
        selected: month.selected,
        today: month.today
      },
      [commonStyles.button]
    );

    return (
      <button
        type="button"
        key={ `month_${ month.value }` }
        className={ classes }
        tabIndex={ -1 }
        onClick={ (): void => {
          if (!month.disabled) onChange(month, index);
        } }
      >
        { month.name }
      </button>
    );
  });

  return <div className={ commonStyles['grid-column4'] }>{ months }</div>;
}
